<template>
  <div id="app">
    <router-view/>
    <mt-tabbar v-model="message" fixed>
      <mt-tab-item id="home">
        <img slot="icon" :src="iconArr[0]">
        首页
      </mt-tab-item>
      <mt-tab-item id="hire">
        <img slot="icon" :src="iconArr[1]">
        招聘信息
      </mt-tab-item>
      <mt-tab-item id="person">
        <img slot="icon" :src="iconArr[2]">
        个人中心
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      message: 'home',
      iconArr: ['../static/home-check.png', '../static/hire-normal.png', '../static/personal-normal .png']
    }
  },
  watch: {
    message: function (val, oldVal) {
      this.$router.push({
          path: '/' + val
        })
      if (val == 'home') {
        this.iconArr = ['../static/home-check.png', '../static/hire-normal.png', '../static/personal-normal .png']
      } else if (val == 'hire') {
        this.iconArr = ['../static/home-normal.png', '../static/hire-check.png', '../static/personal-normal .png']
      } else {
        this.iconArr = ['../static/home-normal.png', '../static/hire-normal.png', '../static/personal-check .png']
      }
    }
  },
  created () {
  }
}
</script>

<style>
body {
  margin: 0;
  box-sizing: border-box;
  background-color: rgb(245, 245, 245);
  margin-bottom: 55px;
}

</style>
<style>
.mint-tabbar {
  background-color: #fff;
}
.mint-tabbar > .mint-tab-item.is-selected {
  background-color: #fff;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#d9d9d9), color-stop(50%, #d9d9d9), color-stop(50%, transparent));
  background-size: 100% 1px;
  background-repeat: no-repeat;
}
</style>




