import Vue from 'vue'
import Router from 'vue-router'
import Indexpage from '@/components/index'
import Hire from '@/components/hire'
import Person from '@/components/person'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'indexpage',
      component: Indexpage
    },
    {
      path: '/home',
      name: 'indexpage',
      component: Indexpage
    },
    {
      path: '/hire',
      name: 'hire',
      component: Hire
    },
    {
      path: '/person',
      name: 'person',
      component: Person
    }
  ]
})



